<template>
    <div id="login-container">
        <div class="sl-logo">
            <img src="./images/logo.png" alt="logo">
        </div>

        <div class="login-content">
            <div class="login-title">
              管理后台
            </div>
            <!-- 公众号 start -->
            <div class="login-right">
                <div class="neweat-img">
                    <img src="./images/xinchihuo.png" alt="新吃货公众号">
                </div>
                <p class="neweat-text">扫一扫，关注新吃货公众号</p>
            </div>
            <!-- 公众号 end -->
            <!-- 切换二维码 start -->
            <div class="toggle" @click="toggleLoginWay">
                <img v-if="isShowlogin" src="./images/login1.png" />
                <img v-else src="./images/login2.png" alt="">
            </div>
            <!-- 切换二维码 end -->

            <!-- login start -->
            <div v-if="isShowlogin" class="login-left">
                <el-form class="login-form" :model="loginForm">

                    <!-- username -->
                    <el-form-item class="username">
                        <el-input class="user" 
                        :class="{errtip: isShowErrtip}" 
                        type="text" 
                        placeholder="请输入登录手机号" 
                        v-model="loginForm.username">
                            <template slot="prepend">
                                <img class="icon" src="./images/admin.png" alt="">
                            </template>
                        </el-input>
                    </el-form-item>

                    <!-- password -->
                    <el-form-item>
                        <el-input 
                        @change="getmd5Password" 
                        class="user" 
                        :class="{errtip: isShowErrtip}" 
                        :type="pwdType" 
                        placeholder="请输入密码"
                         v-model="loginForm.password">
                            <template slot="prepend">
                                <img class="icon" src="./images/password.png" alt="">
                            </template>
                        </el-input>
                        <input type="hidden" v-model="loginForm.passwordmd5">
                    </el-form-item>

                    <!-- check-code -->
                    <div class="check-code">
                        <el-form-item class="check-input">
                            <el-input 
                            @change="clearErrTip" 
                            :class="{errtip: isShowCheckErrtip}" 
                            type="text" 
                            placeholder="请输入验证码" 
                            v-model="loginForm.code"></el-input>
                        </el-form-item>
                        <div class="check-img">
                            <img @click="changeCheckImg" :src="codeimgurl" alt="验证图片" />
                        </div>
                        <!-- <div class="reset left">
                            <img @click="changeCheckImg" src="./images/rennew.png" alt="刷新" />
                        </div> -->
                    </div>

                    <!-- 记住密码 -->
                    <div class="password-select clearfix">
                        <el-form-item class="password-remeber left">
                            <el-checkbox v-model="loginForm.checked">记住密码</el-checkbox>
                        </el-form-item>

                        <div class="forget-password right" @click="goforgetPassword">
                            忘记密码？
                        </div>
                    </div>

                    <div class="errTipMsg">{{ errTipMsg }}</div>

                    <el-form-item class="btn-group">
                        <el-button v-if="isShowSubmitBtn" class="submit" type="primary" @click="login">登录</el-button>
                        <el-button v-else class="disabled" type="primary" :disabled="true">登录</el-button>
                    </el-form-item>

                    <div class="regist">
                      还没有账号？
                      <span class="regist-btn" @click="goRegistrate">立即注册</span>
                    </div>
                </el-form>

                
            </div>
            <!-- login end -->

            <!-- 微信二维码 start -->
            <div v-if="!isShowlogin" class="login-left">
              <div class="img-content">
                <img :src="wxlogincode" alt="">
                <div class="wx-text">
                微信扫码登录
                </div>
              </div>

            </div>
            <!-- 微信二维码 end -->
        </div>
    </div>
</template>

<script>
// import _ from 'lodash';
// import { Login } from '../index';
import Cookies from "js-cookie";

import { loginByUsername, loginShop } from "../api";
import { Util } from "@/config/util";
import { errcode } from "@/config/cfg";
import { ls } from "@/config/pageStore";

export default {
  components: {},
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
        passwordmd5: "",
        code: "",
        checked: false
      },
      isShowlogin: true, //二维码btn切换
      pwdType: "password",

      codeimgurl: "", //验证码图片地址
      token: "", //终端标识
      key: "", //key

      isShowErrtip: false,
      isShowCheckErrtip: false,
      errTipMsg: "", //错误提示信息
      isToggle1: false, //动画
      isToggle2: false,

      wxlogincode: "" //微信code
    };
  },
  computed: {
    isShowSubmitBtn() {
      if (
        this.loginForm.username &&
        this.loginForm.password &&
        this.loginForm.code
      ) {
        return true;
      } else {
        return false;
      }
    }
  },
  watch: {
    isShowlogin: function(val) {
      if (val === false) {
        this.msg_reg();
      }
    }
  },
  created() {
    this.token = Util.creatToken();
    this.key = Util.creatKey();

    this.initCheckImg();
    this.getPassword();
    this.initwxcodeLogin();
  },
  mounted() {
    window.WebSock.Init(
      "ws://srv.jzzwlcm.com:13010/websocket",
      this.token,
      this.key,
      () => {
        // this.msg_reg();
      }
    );
  },
  methods: {
    toggleLoginWay() {
      this.isToggle1 = true;
      this.isShowlogin = !this.isShowlogin;
      setTimeout(() => {
        // this.isShowlogin = !this.isShowlogin;
        this.isToggle2 = true;
      }, 3000);
      setTimeout(() => {
        this.isToggle1 = false;
        this.isToggle2 = false;
      }, 6000);
    },
    //登陆成功后处理
    loginHander(resp) {
      if (resp.ret === 0) {
        this.checkRember();
        ls.setItem("public#shopinfo", resp.data, 7);
        ls.setItem("public#userid", resp.data.userinfo.userid, 7);
        ls.setItem("SELECTMENU", "/statistical/orderform", 7); //选中按钮
        Cookies.set("token", this.token); //cookie 写入cookie
        this.$store.commit("CHANGE_USERID", resp.data.userinfo.userid);

        let shopinfo = resp.data.shopinfo || [];

        if (shopinfo.length === 0) {
          this.$router.push({ path: "/personalcenter/userinfo" });
        } else {
          this.$router.push({ path: "/selectshop" });
        }
      } else {
        this.loginErrMsg(resp.ret);
        this.changeCheckImg();
      }
    },
    //账号密码登陆
    login() {
      let username = this.loginForm.username;
      let password = this.loginForm.passwordmd5;
      let checkcode = this.loginForm.code;
      loginByUsername(username, password, checkcode, this.loginHander);
    },
    initCheckImg() {
      this.token = Util.creatToken();
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=120&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1&" +
        Math.random();
    },
    changeCheckImg() {
      this.codeimgurl =
        base_url +
        "/code.php?height=26&width=120&fontsize=16&codelen=4&token=" +
        this.token +
        "&is_plain=1" +
        "&" +
        Math.random();
    },
    initwxcodeLogin() {
      this.token = Util.creatToken();
      let encodeurl = encodeURIComponent(
        "http://192.168.5.117/www.ob.com/php/login_wx.php?token=" + this.token
      );

      this.wxlogincode =
        base_url + "/img_get.php?get_login_qrcode=1&token=" + this.token;
    },
    initform() {
      (this.loginForm.username = ""),
        (this.loginForm.password = ""),
        (this.loginForm.code = ""),
        (this.loginForm.checked = false);
    },
    loginErrMsg: function(ret) {
      switch (ret) {
        case errcode.USER_NO_EXIST:
          this.isShowErrtip = true;
          this.errTipMsg = errcode.toString(ret);
          break;
        case errcode.DATA_PASSWD_ERR:
          this.isShowErrtip = true;
          this.errTipMsg = errcode.toString(ret);
          break;
        case errcode.USER_PASSWD_ERR:
          this.isShowErrtip = true;
          this.errTipMsg = errcode.toString(ret);
          break;
        case errcode.COKE_ERR:
          this.isShowCheckErrtip = true;
          this.errTipMsg = errcode.toString(ret);
          break;
        case errcode.IMG_ERRL:
          this.isShowCheckErrtip = true;
          this.errTipMsg = errcode.toString(ret);
          break;
        default:
          this.errTipMsg = errcode.toString(errcode.SYS_ERR);
      }
    },
    //记住密码
    checkRember() {
      if (this.loginForm.checked === true) {
        let LOGINPD = {};
        LOGINPD["username"] = this.loginForm.username;
        LOGINPD["password"] = this.loginForm.passwordmd5;
        ls.setItem("LOGINPD", LOGINPD, 7);
      } else {
        ls.removeItem("LOGINPD");
      }
    },
    //取密码
    getPassword() {
      this.clearErrTip();
      let loginPd = ls.getItem("LOGINPD");
      if (loginPd !== null) {
        this.loginForm.username = loginPd.username;
        this.loginForm.password = "默认密码！！";
        this.loginForm.passwordmd5 = loginPd.password;
        this.loginForm.checked = true;
      }
    },
    clearErrTip() {
      this.isShowErrtip = false;
      this.isShowCheckErrtip = false;
      this.errTipMsg = "";
    },
    getmd5Password() {
      this.loginForm.passwordmd5 = $.md5(this.loginForm.password);
      this.clearErrTip();
    },
    goforgetPassword() {
      this.$router.push("/forgetpassword");
    },
    goRegistrate() {
      this.$router.push("/registrate");
    },
    //微信扫码登陆
    msg_reg() {
      Util.RecWxLogininfo(this.token, resp => {
        if (resp.ret === 0) {
          let data = {
            login_wx: 1,
            user_id: resp.userid
          };
          loginShop(data, this.loginHander);
        }
      }, this.$slnotify);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#login-container {
  width: 100%;
  height: 100vh;
  background-image: url(./images/loadingpage.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.sl-logo {
  position: fixed;
  @include tl(19.6%, 50%);
  @include wh(230px, 78px);
  margin-left: -115px;

  img {
    @include wh(230px, 78px);
  }
}

.login-content {
  .login-title {
    height: 61px;
    background: #eff3fa;
    font-size: 20px;
    text-align: center;
    line-height: 61px;
    font-weight: bold;
    color: #4877e7;
    letter-spacing: 0.71px;
  }
  .login-left {
    padding: 0 70px;
  }
  .login-right {
    @include wh(160px, 200px);
    position: absolute;
    top: 0;
    right: -180px;
    background-color: #fff;
    border-top: 4px solid $blue;

    .neweat-img {
      @include wh(120px, 120px);
      margin: 20px;
    }

    .neweat-text {
      @include fc(12px, #000);
      text-align: center;
    }
  }

  position: fixed;
  @include tl(32.4%, 50%);
  @include wh(540px, 455px); // transform: translate(-50%, 0);
  margin-left: -270px;
  background-color: #fff;

  .toggle {
    cursor: pointer;
    @include wh(60px, 60px);
    position: absolute;
    bottom: 0;
    right: 0;
    img {
      @include wh(60px, 60px);
    }
  }
}

.login-form {
  .icon {
    @include wh(16px, 16px);
  }

  .submit {
    width: 100%;
    background-color: $blue;
  }

  .disabled {
    width: 100%;
    background-color: #d8d8d8;
  }

  .check-code {
    position: relative;
    // .check-input {
    //   width: 160px;
    // }
    .check-img {
      cursor: pointer;
      position: absolute;
      top: 4px;
      right: 4px;
      @include wh(120px, 26px);
      margin-left: 10px;
    }
    .reset {
      height: 35px;
      padding-top: 6px;
      margin-left: 20px;

      img {
        @include wh(24px, 24px);
        cursor: pointer;
      }
    }
  }

  .password-select {
    .forget-password {
      @include fc(12px, #4877e7);
      height: 36px;
      line-height: 36px;
      cursor: pointer;
    }
  }

  .errTipMsg {
    @include fc(12px, #E7487E);
    height: 50px;
  }
}

.btn-group {
  margin-bottom: 10px !important;
}
.regist {
  text-align: center;
  @include fc(12px, #333);
  .regist-btn {
    color: #4877e7;
    cursor: pointer;
  }
}

.toggle1 {
  animation-duration: 3s;
  animation-name: toggle;
}

.toggle2 {
  animation-duration: 3s;
  animation-name: toggle1;
}

@keyframes toggle {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(90deg);
  }
}

@keyframes toggle1 {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}

.img-content {
  @include wh(200px, 200px);
  margin-top: 80px;
  margin-left: 100px;
  img {
    @include wh(200px, 200px);
  }
}
.wx-text {
  text-align: center;
  @include fc(14px, #333333);
}
</style>

<style lang="scss">
#login-container {
  input.el-input__inner {
    height: 34px;
    border-radius: 0;
  }
  div.el-input-group__prepend {
    border-radius: 0;
  }
  div.user {
    .el-input__inner {
      border-left: 0;
    }
  }
  input.el-input__inner:hover {
    border-color: #bfcbd9;
  }
  input.el-input__inner:focus {
    border-color: #bfcbd9;
  }

  .el-form-item {
    margin-bottom: 20px;
  }
  .el-form-item.username {
    margin-top: 60px;
  }
  .el-form-item.check-input {
    margin-bottom: 0;
  }
  .el-form-item.password-remeber {
    margin-bottom: 0;
  }

  .errtip {
    .el-input-group__prepend {
      border-color: red;
    }
    input {
      border-color: red;
    }
    input.el-input__inner:hover {
      border-color: red;
    }
    input.el-input__inner:focus {
      border-color: red;
    } // input
  }
  .el-form-item__content button.el-button--primary.submit:hover {
    background-color: #3254a5;
    border-color: #3254a5;
  }
  .el-form-item__content button.el-button--primary.disabled {
    border-color: #c4c4c4;
  }
  .el-form-item__content {
    line-height: 34px;
  }
}
</style>



